import { render } from '@testing-library/react'
import { useState } from 'react'
import './SharePane.scss'

interface SharePaneProps {
  isOpenSharePane: boolean
  onClickCloseSharePane: () => void
  onClickOpenSideBarShare: () => void
}

function SharePane(props: SharePaneProps) {

  let { isOpenSharePane, onClickCloseSharePane, onClickOpenSideBarShare } = props;

  const [isTickAvailable, setIsTickAvailable] = useState<boolean>(false);

  const [shareProfile, setShareprofile] = useState([
    {
      id: '1',
      name: 'Smith Mathew',
      imageUrl: 'images/share_profile_pic/profile-pic1.png',
      isTick: false
    },
    {
      id: '2',
      name: 'Monica Randawa',
      imageUrl: 'images/share_profile_pic/profile-pic2.png',
      isTick: false
    },
    {
      id: '3',
      name: 'John Walton',
      imageUrl: 'images/share_profile_pic/profile-pic3.png',
      isTick: false
    },
    {
      id: '4',
      name: 'Merry An',
      imageUrl: 'images/share_profile_pic/profile-pic4.png',
      isTick: false
    },
    {
      id: '5',
      name: 'Robert Parker',
      imageUrl: 'images/share_profile_pic/profile-pic5.png',
      isTick: false
    },
    {
      id: '6',
      name: 'Vivienne Westwood',
      imageUrl: 'images/share_profile_pic/profile-pic6.png',
      isTick: false
    },
    {
      id: '7',
      name: 'Rick Owens',
      imageUrl: 'images/share_profile_pic/profile-pic7.png',
      isTick: false
    }
  ])

  const findTick = () => {

    let isFound = false;

    for (let i = 0; i < shareProfile.length; i++) {
      if (shareProfile[i].isTick) {
        isFound = true;
      }
    }

    if (isFound) {
      return true
    } else {
      return false
    }

  }

  const [isShowMessagePane, setShowMessagePane] = useState<boolean>(false)

  const handleTickProfile = (index) => {
    let myshareProfile = shareProfile.slice()
    myshareProfile[index].isTick = !myshareProfile[index].isTick
    setShareprofile(myshareProfile)
    if (findTick()) {
      setShowMessagePane(true)
    } else {
      setShowMessagePane(false)
    }

  }

  return (
    <div className={`d-flex flex-column share-panezone ${isOpenSharePane ? 'show' : ''} ${isShowMessagePane ? 'show-messagepane' : ''}`}>
      <div className='d-flex share-panezone-header justify-content-space-between'>
        <div onClick={onClickCloseSharePane} className='cross-icon cursor-pointer'>
          <img className='crossicon_pic' src='images/icons/cross_icon.png' alt='cross_icon' width='13px' height='13px' />
        </div>
        <div className='headername'>
          แชร์กับ
        </div>
        <div className='searchname cursor-pointer'>
          {
            isShowMessagePane ?
              <div className='cursor-pointer sharelink'>
                แชร์
              </div>
              :
              <img src="images/chatscreen/magnifierIcon.png" alt="magnifier" onClick={onClickOpenSideBarShare}></img>
          }
        </div>
      </div>
      <div className='d-flex share-panezone-content'>
        <ul className='d-flex flex-wrap-wrap'>
          {
            shareProfile.map((shareProfiles, index) => {
              return (
                <li key={index} className={`d-flex flex-column cursor-pointer`} onClick={() => handleTickProfile(index)}>
                  <img className='profile-pic-image' src={shareProfiles.imageUrl} alt="profile_pic" width='42px' />
                  {
                    shareProfiles.isTick ?
                      <img className='profile-tick-image' src="images/share_profile_pic/profile_tick.png" alt="profile_tick_image" width='39px' />
                      :
                      <></>
                  }
                  <div className='profile-pic-name'>
                    {shareProfiles.name}
                  </div>
                </li>
              )
            })
          }
          < li className="d-flex flex-column cursor-pointer addmorezone">
            <img className='profile-pic-image' src="images/share_profile_pic/add_more_profile.png" width='53px' />
            <div className='profile-pic-name'>
              ดูเพิ่ม
            </div>
          </li>
        </ul>
      </div>
      <div className='share-message-input'>
        <textarea placeholder='พิมพ์ข้อความ' />
      </div>
    </div >
  )
}

export default SharePane