import './AddNewContact.scss'
import React, { useState } from 'react'

interface AddNewContactProps {
	onClickCloseAddCustomer: () => void
	platformToken: string
}

function AddNewContact(props: AddNewContactProps) {

	let { onClickCloseAddCustomer, platformToken } = props;
	let [checkActive, setCheckActive] = useState<boolean>(false);

	const [listForm, setlistForm] = useState([
		{ id: 1, name: 'ชื่อ', type: 'input', placeholder: 'โปรดระบุ', value: '' },
		{ id: 2, name: 'นามสกุล', type: 'input', placeholder: 'โปรดระบุ', value: '' },
		{ id: 3, name: 'แหล่งที่มา', type: 'dropdown', placeholder: 'โปรดระบุ', value: '' },
		{ id: 4, name: 'เบอร์โทร', type: 'input', placeholder: 'โปรดระบุ', value: '' },
		{ id: 5, name: 'เวลาสะดวกที่ติดต่อ', type: 'time', placeholder: '00.00 น.', value: '' },
		{ id: 6, name: 'Facebook', type: 'input', placeholder: 'โปรดระบุ', value: '' },
		{ id: 7, name: 'Line ID', type: 'input', placeholder: 'โปรดระบุ', value: '' },
		{ id: 8, name: 'Email', type: 'input', placeholder: 'โปรดระบุ', value: '' },
	])

	const handleOnChangeForm = (id, event) => {
		const myList = listForm.slice();
		for (let i = 0; i < myList.length; i++) {
			if (myList[i].id === id) {
				myList[i].value = event.target.value
			}
		}
		setCheckActive(checkActivefunc())
		setlistForm(myList)
	}

	const checkActivefunc = () => {
		let isActive = true;
		const myList = listForm.slice();
		for (let i = 0; i < myList.length; i++) {
			if (myList[i].value === '') {
				isActive = false;
			}
		}
		return isActive;
	}


	return (
		<>
			<div className='d-flex header'>
				<img onClick={onClickCloseAddCustomer} width='23px' height='23px' src="images/addnew-back.png" alt="addnew_back" className='cursor-pointer' />
				<span className='title'>เพิ่มลูกค้าไหม่</span>
				<div></div>
			</div>
			<div className='d-flex flex-column form'>
				<form action="/action_page.php">
					{listForm.map((listForms, index) => {
						return (
							<div key={index}>
								<label>{listForms.name}</label><span className='required'>*</span><br />
								{
									listForms.type === 'input' ?
										<input onChange={(event) => handleOnChangeForm(listForms.id, event)} type="text" placeholder={listForms.placeholder} value={listForms.value} />
										:
										<></>
								}
								{
									listForms.type === 'dropdown' ?
										<select defaultValue={'โปรดเลือก'} onChange={(event) => handleOnChangeForm(listForms.id, event)} id="cars" name="cars" >
											<option value="โปรดเลือก" disabled>โปรดเลือก ...</option>
											<option value="volvo">ทดสอบ1</option>
											<option value="saab">ทดสอบ2</option>
										</select>
										:
										<></>
								}
								{
									listForms.type === 'time' ?
										<input onChange={(event) => handleOnChangeForm(listForms.id, event)} type="time" placeholder={listForms.placeholder} value={listForms.value} />
										:
										<></>
								}
								<br />
							</div>
						)
					})}
					<div className={`submitbutton ${checkActive ? 'active' : ''} ${platformToken}`}>
						<input type="submit" value="บันทึก" />
					</div>
				</form>
			</div>
		</>
	)
}

export default AddNewContact
