import { useEffect, useRef, useState } from 'react'
import './ChatMessagePage.scss'
import { ConnectContactList } from 'components/ConnectContactList'
import { AddNewContact } from 'components/AddNewContact'
import { SharePane } from 'components/SharePane'
import { SideSharePane } from 'components/SideSharePane'
import Calendar from 'react-calendar'
import 'react-calendar/dist/Calendar.css'

interface ChatMessagePageProps {
	onClickBackStep: () => void
	platformtoken: string
}

interface TickInfo {
	id: string;
	name: string;
	imageUrl: string;
	type: string;
	message: string;
	time: string;
	isShare: boolean;
	isReplied: boolean;
	re_message: string;
	replied_name: string;
	replied_pic: string;
	enableTooltip: boolean

}



function ChatMessagePage(props: ChatMessagePageProps) {

	let { platformtoken, onClickBackStep } = props

	const [text, setText] = useState<string>('');
	const textareaRef = useRef<HTMLTextAreaElement>(null);
	const [isAppearContactList, setAppearContactList] = useState<boolean>(false);
	const [isBackDropContactListZone, setBackdropContactListZOne] = useState<boolean>(false);

	const [isContactListZone, setContactListZone] = useState<boolean>(false);

	const [isOpenSuccessContactList, setOpenSuccessContaactList] = useState<boolean>(false);

	const [isAlreadyAdd, setisAlreadyAdd] = useState<boolean>(false)

	const [valAddMoreCustomerPane, setAddMoreCustomerPane] = useState<string>('300px')

	const [isEnableReplied, setEnableReplied] = useState<boolean>(false)

	const [enableRepliedInfo, setEnableRepliedInfo] = useState({
		name: '',
		imageUrl: '',
		message: '',
	})

	const [isOpenSharePane, setOpenSharePane] = useState<boolean>(false)

	const [isViewSelectSharecontact, setViewSelectSharecontact] = useState<boolean>(false)

	const [isShowSideBarShare, setShowSideBarShare] = useState<boolean>(false)

	const [isViewDialogueOption, setViewDialogueOption] = useState<boolean>(false)

	const [isOpenSearchHistory, setOpenSearchHistory] = useState<boolean>(false)

	const [calendarDate, setcalendarDate] = useState<string>('')

	const [isOpenCalendarZone, setIsOpenCalendarZone] = useState<boolean>(false)

	const [listMessage, setListMessage] = useState([
		{
			id: 'ch1',
			name: 'Kate Rose',
			imageUrl: 'images/chatscreen/chatpeople/chatpeople-1.png',
			type: '1',
			message: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been simply dummy text of the printing and typesetting industry',
			time: '12:00', isShare: false,
			isReplied: false,
			re_message: '',
			replied_name: '',
			replied_pic: '',
			enableTooltip: false
		},
		{
			id: 'ch2',
			name: 'John Doe',
			imageUrl: 'images/chatscreen/chatpeople/chatpeople-2.png',
			type: '2',
			message: 'Lorem Ipsum is simpl',
			time: '12:00',
			isShare: false,
			isReplied: false,
			re_message: '',
			replied_name: '',
			replied_pic: '',
			enableTooltip: false
		},
		{
			id: 'ch3',
			name: 'Kate Rose',
			imageUrl: 'images/chatscreen/chatpeople/chatpeople-1.png',
			type: '1',
			message: 'Lorem Ipsum is simply.',
			time: '12:00',
			isShare: false,
			isReplied: false,
			re_message: '',
			replied_name: '',
			replied_pic: '',
			enableTooltip: false
		},
		{
			id: 'ch4',
			name: 'John Doe',
			imageUrl: 'images/chatscreen/chatpeople/chatpeople-2.png',
			type: '2',
			message: 'Test',
			time: '12:00',
			isShare: false,
			isReplied: true,
			re_message: 'Lorem Ipsum is simply ',
			replied_name: 'Kate Rose',
			replied_pic: 'images/chatscreen/chatpeople/chatpeople-1.png',
			enableTooltip: false
		},
	])

	const [tickedList, setTickedList] = useState<{
		id: string;
		name: string;
		imageUrl: string;
		type: string;
		message: string;
		time: string;
		isShare: boolean;
		isReplied: boolean;
		re_message: string;
		replied_name: string;
		replied_pic: string;
		enableTooltip: boolean
	}[]>()

	const handleTextChange = (event) => {
		setText(event.target.value)
	}

	const handleTextKeyDown = (event) => {
		if (event.which === 13 && !event.shiftKey) {
			event.preventDefault()
			handleSendClick()
		}
	}

	const handleSendClick = async () => {

	}

	const handleTextIndi = () => {
		let myTextArea = document.getElementById('myText');
		let textIndi = document.getElementById('text_indi');

		if (myTextArea != null && textIndi != null) {
			myTextArea.style.width = '210px';
			textIndi.style.display = 'none';
		}
	}

	useEffect(() => {
		if (textareaRef && textareaRef.current) {

			if (textareaRef.current.value.length % 26 === 0 && textareaRef.current.value.length < 104) {
				textareaRef.current.style.height = '20px'
				const scrollHeight = textareaRef.current.scrollHeight
				textareaRef.current.style.height = scrollHeight + 'px'
			}
		}

	}, [text])

	const handleFocus = () => {

		let myTextArea = document.getElementById('myText');
		let textIndi = document.getElementById('text_indi');

		if (myTextArea != null && textIndi != null) {
			myTextArea.style.width = '250px';
			textIndi.style.display = 'block';
		}

	}

	const handleBlur = () => {
		let myTextArea = document.getElementById('myText');
		let textIndi = document.getElementById('text_indi');

		if (myTextArea != null && textIndi != null) {
			myTextArea.style.width = '210px';
			textIndi.style.display = 'none';
		}
	}

	const handleAppearContactList = () => {
		setAppearContactList(!isAppearContactList);
	}

	const handleBackDropContactListZone = (is_alreadyadd) => {
		setBackdropContactListZOne(!isBackDropContactListZone);
		setContactListZone(!isContactListZone);
		setAppearContactList(!isAppearContactList);
		const el = document.getElementById("myContactList");

		if (el !== undefined && el !== null) {
			console.log("ok");
			document.getElementById("myContactList")?.focus();
		}

		setisAlreadyAdd(is_alreadyadd)
	}

	const handleCloseContactList = () => {
		setBackdropContactListZOne(!isBackDropContactListZone);
		setContactListZone(!isContactListZone);
	}

	const handleSuccessContanctList = () => {
		setOpenSuccessContaactList(!isOpenSuccessContactList)
		setContactListZone(!isContactListZone);
	}

	const handleCloseAll = () => {
		setContactListZone(false)
		setOpenSuccessContaactList(false)
		setAppearContactList(false)
		setBackdropContactListZOne(false)
	}

	const handleAddMoreCustomer = () => {
		setAddMoreCustomerPane('show');
	}

	const handleCloseAddMoreCustomer = () => {
		setAddMoreCustomerPane('');
	}

	const handleOpenToolTip = (event, id, lsm, index) => {
		event.preventDefault();
		let myListMessages = listMessage.slice()
		myListMessages[index].enableTooltip = !myListMessages[index].enableTooltip;
		setListMessage(myListMessages)
		const a = document.getElementById(id)
		a?.focus();
	}

	const handleCloseToolTip = (index) => {
		let myListMessages = listMessage.slice()
		myListMessages[index].enableTooltip = false;
		setListMessage(myListMessages)
	}

	const handleActiveEnableReply = (index) => {
		let myListMessages = listMessage.slice()
		let myEnableReplied = enableRepliedInfo
		myEnableReplied.name = myListMessages[index].name
		myEnableReplied.imageUrl = myListMessages[index].imageUrl
		myEnableReplied.message = myListMessages[index].message
		myListMessages[index].enableTooltip = false;
		setEnableRepliedInfo(myEnableReplied)
		setEnableReplied(true)
		setListMessage(myListMessages)
	}

	const isCancleEnableReply = () => {
		setEnableReplied(false);
	}

	const handleSharedTick2 = (con, index) => {
		let myListMessages = listMessage.slice()

		if (!listMessage[index].isShare) {
			if (tickedList !== undefined && tickedList?.length > 0) {
				let myTickedList = tickedList?.slice()
				myTickedList?.push(listMessage[index])
				setTickedList(myTickedList);
			} else {
				let myTickedList: TickInfo[] = []
				myTickedList?.push(listMessage[index])
				setTickedList(myTickedList);
			}
			console.log(tickedList)

		} else {
			const result = tickedList?.findIndex(tickedList => tickedList.id !== myListMessages[index].id);
			if (result !== undefined) {
				const list = tickedList?.splice(result, 1)
				setTickedList(list);
				console.log(list);
			}
		}

		myListMessages[index].isShare = !listMessage[index].isShare
		setListMessage(myListMessages)

	}

	const handleShareContact = (index) => {
		let myListMessages = listMessage.slice()
		myListMessages[index].enableTooltip = false;
		setViewSelectSharecontact(true)
		setListMessage(myListMessages)
	}

	const handleCancleShareContact = () => {
		setViewSelectSharecontact(false)
	}

	const handleSharePaneOpen = () => {
		setOpenSharePane(true);
		setBackdropContactListZOne(true)
	}

	const handleCloseSharePane = () => {
		setOpenSharePane(false);
		setViewSelectSharecontact(false)
		setBackdropContactListZOne(false)
		setOpenSharePane(false);
	}

	const handleCloseSidebarShare = () => {
		setShowSideBarShare(false);
		setBackdropContactListZOne(false)
		setOpenSharePane(false);
		setViewSelectSharecontact(false)
	}

	const onClickOpenSideBarShare = () => {
		setShowSideBarShare(true);
		setBackdropContactListZOne(false)
	}

	const onClickShareSuccessTrigger = () => {
		setShowSideBarShare(false);
		setBackdropContactListZOne(true);
		setOpenSharePane(false);
		setViewSelectSharecontact(false)
	}

	const closeBackDropOnSidebarShare = () => {
		setBackdropContactListZOne(false);
	}

	const handleViewDialogueOption = () => {
		setViewDialogueOption(!isViewDialogueOption);
	}

	const handleIsOpenSearchHistory = () => {
		setOpenSearchHistory(!isOpenSearchHistory)
	}

	const handleCalendarChange = (value: Date) => {
		const day = String("0" + value.getDay()).slice(-2);
		const month = String("0" + (value.getMonth() + 1)).slice(-2);
		const year = value.getFullYear()

		setcalendarDate(day + "/" + month + "/" + year)
		setIsOpenCalendarZone(false);
	}

	const handleCalendarZone = () => {
		setIsOpenCalendarZone(!isOpenCalendarZone);
	}

	const handleBackStep = (isOpenHist, func) => {
		if (isOpenHist) {
			setOpenSearchHistory(!isOpenSearchHistory)
		} else {
			func()
		}
	}

	return (
		<>
			<div data-testid='chatmessagepageheader' className={`message-page-header ${platformtoken}`}>
				<div className='d-flex header-wrapper search-history-zone'>
					<div className='back-step-zone'>
						<img onClick={() => handleBackStep(isOpenSearchHistory, onClickBackStep)} className='backstep-image' src='images/chatscreen/backstep_button.png' alt="backstepimage" width='21px'></img>
					</div>
					{!isOpenSearchHistory ?
						<>
							{
								platformtoken === 'linetoken' ?
									<>
									</>
									:
									<>
										<div className='image-zone'>
											<img className='chat-people-header' src='images/chatscreen/chatpeople/chatpeople-1.png' alt="chat-people-header" width='43px'></img>
										</div>
									</>
							}
							<div className='profile-name'>
								<span className='message-page-header-username'>Kate Rose</span>
							</div>
							<div className='option-zone'>
								<img onClick={handleIsOpenSearchHistory} className='search-history-icon' src='images/chatscreen/message_history_search.png' alt='search-history-icon' width='32px'></img>
								<img onClick={handleAppearContactList} className='message-page-fishdot' alt='message-page-fishdot' src='images/chatscreen/fishdot_icon.png' width='5px'></img>
								<div className={isAppearContactList ? 'contactlist show' : 'contactlist '} data-testid='contact-list'>
									<ul>
										<li>
											<img
												src="images/icons/star.png"
												alt="addUser"
												width="15px"
												height="15px"
											/>
											<span>ติดต่อ</span>
										</li>
										<li onClick={() => handleBackDropContactListZone(false)}>
											<img
												src="images/icons/addUser.svg"
												alt="addUser"
												width="15px"
												height="15px"
											/>
											<span>เชื่อมต่อรายชื่อ</span>
										</li>
										<li onClick={() => handleBackDropContactListZone(true)}>
											<img
												src="images/icons/addUser.svg"
												alt="addUser"
												width="18px"
												height="18px"
											/>
											<span>เชื่อมต่อรายชื่อเดิม</span>
										</li>
									</ul>
								</div>
							</div>
						</>
						:
						<div className='myInput'>
							<input type="text" placeholder='Search' />
							<img src="images/chatscreen/magnifierIcon.png" alt="magnifier" width="16px"></img>
						</div>
					}
				</div>

			</div>

			<div className={`message-page-content ${platformtoken}`}>
				<div className={`chat-message-zone ${isOpenSearchHistory ? 'history-offset' : ''} `}>
					<ul>
						{listMessage.map((listMessages, index) => {
							return (
								<div key={index}>
									{
										listMessages.type === '1' && listMessages.isReplied === false ?
											<li onContextMenu={(event) => handleOpenToolTip(event, listMessages.id, listMessages, index)} className='d-flex send-chat-message'>
												<div className={`share-tickzone ${isViewSelectSharecontact ? '' : 'd-none'}`}>
													{listMessages.isShare ?
														<img alt='tickzone_tick' src="images/icons/tick_contactlist.png" className="tickzone cursor-pointer" onClick={() => handleSharedTick2(listMessages, index)} width='26px'></img>
														:
														<img alt='tickzoneuntick' src="images/icons/untick_contactlist.png" className="tickzone cursor-pointer" onClick={() => handleSharedTick2(listMessages, index)} width='26px'></img>
													}
												</div>
												<img className='send-chat-message-profilepic' src={listMessages.imageUrl} alt='send_chat_profile_pic' width='43px' height='43px'></img>
												<div tabIndex={0} onBlur={() => handleCloseToolTip(index)} id={listMessages.id} className={`send-chat-message-content tooltip ${listMessages.enableTooltip ? 'active-tooltip' : ''}`}   >{listMessages.message}
													{
														listMessages.enableTooltip ?
															<div className="d-flex tooltiptext justify-content-space-around">
																<div className="d-flex flex-column reply-tooltip cursor-pointer" onClick={() => handleActiveEnableReply(index)}>
																	<img width='30px' height='auto' src="images/icons/reply_icon.png" alt="reply_icon" />
																	<div className='replytooltip-text'>ตอบกลับ</div>
																</div>
																<span className='separation-line'></span>
																<div className="d-flex flex-column share-tooltip cursor-pointer" onClick={() => handleShareContact(index)}>
																	<img width='30px' height='auto' src="images/icons/share_icon.png" alt="share_icon" />
																	<div className='share-tooltiptext'>แชร์</div>
																</div>
															</div>
															:
															<></>
													}
												</div>
												<div className='send-chat-message-time'>
													<span>
														{listMessages.time}
													</span>
												</div>
											</li>
											:
											<>
											</>
									}

									{
										listMessages.type === '2' ?
											<li className='d-flex justify-content-end recieve-chat-message'>
												<div className={`share-tickzone ${isViewSelectSharecontact ? '' : 'd-none'}`}>
													{listMessages.isShare ?
														<img alt='tickzone_tick' src="images/icons/tick_contactlist.png" className="tickzone cursor-pointer" onClick={() => handleSharedTick2(listMessages, index)} width='26px'></img>
														:
														<img alt='tickzoneuntick' src="images/icons/untick_contactlist.png" className="tickzone cursor-pointer" onClick={() => handleSharedTick2(listMessages, index)} width='26px'></img>
													}
												</div>
												<div className='recieve-chat-message-time d-flex flex-column'>
													<div>{listMessages.time}</div>
												</div>
												{
													listMessages.isReplied ?
														<div className={`recieve-chat-message-content replied-include ${platformtoken}`} >
															<div className='d-flex'>
																<div>
																	<img className='replied-message-zone-profilepic' src={listMessages.replied_pic} alt='send_chat_profile_pic' width='20px'></img>
																</div>
																<div className={`replied-message-zone-info`}>
																	<div className='replied-name'>
																		{listMessages.replied_name}
																	</div>
																	<div className='replied-message-text'>
																		{listMessages.re_message}
																	</div>
																</div>
															</div>
															<div className='replied-separation'></div>
															<div className='message-text'>
																{listMessages.message}
															</div>
														</div>
														:
														<div className={`recieve-chat-message-content ${platformtoken}`} >{listMessages.message}</div>
												}
											</li>
											:
											<>
											</>
									}
								</div>
							)
						})}
					</ul>
				</div>

				<div className={`message-page-footerzone ${platformtoken} ${isOpenSearchHistory ? 'history-offset' : ''}`}>
					{
						!isOpenSearchHistory ?
							<>
								<div className='d-flex footerwrapper'>
									<div className='message-footer-addimage'>
										<img onClick={handleViewDialogueOption} id="image_add" className="chatmessage-imageadd" src="images/chatscreen/chatmessage_imageadd.png" alt="chatmessage_imageadd" width="24px"></img>
										<img onClick={handleViewDialogueOption} className="line_pic_add cursor-pointer" alt='line_pictureadd' src='images/chatscreen/pictureadd_line.png' width='36px'></img>
										<img className="chatmessage-imageicon" src="images/chatscreen/chatmessage_imageicon.png" alt="chatmessage_imageicon" width="24px"></img>
										<img onClick={handleTextIndi} id="text_indi" className='text-indi' src="images/chatscreen/text_indi.png" alt="text_indi" width="36px"></img>
									</div>
									<div id="myText" className='message-footer-textmessagebox'>
										<textarea
											value={text}
											ref={textareaRef}
											placeholder='Aa'
											onChange={handleTextChange}
											onKeyDown={handleTextKeyDown}
											onFocus={() => handleFocus()}
											onBlur={() => handleBlur()}
										></textarea><img className='emoji-image' src='images/chatscreen/emoji_popup.png' alt='emoji_pup' width='25px'></img>
									</div>
								</div>
								<div className={`d-flex add-more-options ${isViewDialogueOption ? 'show' : ''} ${isOpenSearchHistory ? 'd-none' : ''}`}>
									<div className='add-more-options-wrapper cursor-pointer'>
										<div className='options-pics'>
											<img className="addfile-options cursor-pointer" alt='addfile_options' src='images/icons/add_file_options.png' width='36px'></img>
										</div>
										<div className='options-name'>
											แนบไฟล์
										</div>
									</div>
									<div className='add-more-options-wrapper cursor-pointer'>
										<div className='options-pics'>
											<img className="sharelocation-options cursor-pointer" alt='sharelocation_options' src='images/icons/sharelocation_icon.png' width='36px'></img>
										</div>
										<div className='options-name'>
											แชร์ที่ตั้ง
										</div>
									</div>
								</div>
							</>
							:
							<>
								<div className={`d-flex history-date-bar justify-content-space-between ${platformtoken}`}>
									<div className={`calendar-zone ${isOpenCalendarZone ? '' : 'd-none'}`}>
										<Calendar onChange={(value) => handleCalendarChange(value)} />
									</div>
									<div className='d-flex view-find-zone '>
										<div className='goup-image'>
											<img className='cursor-pointer' src="images/chatscreen/backstep_button.png" alt="back_step" width="20px" height="20px" />
										</div>
										<div className='godown-image'>
											<img className='cursor-pointer' src="images/chatscreen/backstep_button.png" alt="back_step" width="20px" height="20px" />
										</div>
									</div>
									<div className='date-result'>{calendarDate}</div>
									<div className='calendar-icon cursor-pointer'>
										<img onClick={handleCalendarZone} src="images/icons/calendar_icon.png" alt="calendar_icon" width="25px" />
									</div>
								</div>
							</>
					}
				</div>
				<div className={`d-flex replied-message-zone  ${isEnableReplied ? 'show' : ''}`}>
					<div>
						<img className='replied-message-zone-profilepic' src={enableRepliedInfo.imageUrl} alt='send_chat_profile_pic' width='24px'></img>
					</div>
					<div className={`replied-message-zone-info`}>
						<div className='replied-name'>
							{enableRepliedInfo.name}
						</div>
						<div className='replied-message-text'>
							{enableRepliedInfo.message}
						</div>
						<img onClick={isCancleEnableReply} className="close-replied-zone cursor-pointer" src="images/icons/cross_icon.png" alt="cross_icon" width="13px" height="13px" ></img>
					</div>
				</div>
			</div>

			<div className={`${platformtoken} ${isBackDropContactListZone ? 'backdrop-panel show' : 'backdrop-panel '}`}>
				<ConnectContactList
					isContactListZone={isContactListZone}
					isAlreadyAdd={isAlreadyAdd}
					platformToken={platformtoken}
					onClickClose={handleCloseContactList}
					openSuccessContact={handleSuccessContanctList}
					isOpenSuccessContactList={isOpenSuccessContactList}
					onClickCloseAll={handleCloseAll}
					onClickHandleAddmoreCustomer={handleAddMoreCustomer}
				></ConnectContactList>
				<div className={`add-new-contact ${valAddMoreCustomerPane} `}>
					<AddNewContact
						onClickCloseAddCustomer={handleCloseAddMoreCustomer}
						platformToken={platformtoken}
					></AddNewContact>
				</div>
			</div>
			<div className={`d-flex justify-content-center sharebutton-zone ${platformtoken} ${isViewSelectSharecontact ? 'show' : ''}`}>
				<button onClick={handleSharePaneOpen} className='cursor-pointer'>แชร์ {tickedList !== undefined && tickedList?.length > 0 ? "(" + tickedList.length + ")" : ''}</button>
				<img onClick={handleCancleShareContact} className="close-shared-zone cursor-pointer" src="images/icons/cross_icon.png" alt="cross_icon" width="10px" height="10px" ></img>
			</div>
			<div className={`${platformtoken}`}>
				<SharePane
					isOpenSharePane={isOpenSharePane}
					onClickCloseSharePane={handleCloseSharePane}
					onClickOpenSideBarShare={onClickOpenSideBarShare}
				></SharePane>
				<SideSharePane
					onClickCloseSideBarShare={handleCloseSidebarShare}
					isShowSideBarShare={isShowSideBarShare}
					onClickShareSuccessTrigger={onClickShareSuccessTrigger}
					onClickCloseSuccessTrigger={closeBackDropOnSidebarShare}
				></SideSharePane>
			</div>
		</>
	);
};
//ทดาอบ
export default ChatMessagePage;
