import './InboxFacebookChat.scss';
import { useRef } from 'react'
import { useState } from 'react'

interface InboxFacebookChatProps {
    onClickChatMessagePage: () => void,
    platformtoken: string
}

function InboxFacebookChat(props: InboxFacebookChatProps) {

    let { platformtoken } = props

    const liRef = useRef<HTMLLIElement>(null);
    const liRef2 = useRef<HTMLLIElement>(null);

    const [search_value, setSearchValue] = useState<string>()

    const [inboxMessageList] = useState<{
        id: number,
        imageUrl: string,
        name: string,
        message: string,
        sent_time: string,
        unread_count: number
    }[]>([
        {
            id: 1,
            imageUrl: 'images/chatscreen/chatpeople/chatpeople-1.png',
            name: 'Kate Rose',
            message: 'ข้อความลูกค้า',
            sent_time: '11.28',
            unread_count: 12
        },
        {
            id: 2,
            imageUrl: 'images/chatscreen/chatpeople/chatpeople-2.png',
            name: 'John Doe',
            message: 'ข้อความลูกค้า',
            sent_time: '11.28',
            unread_count: 12
        },
        {
            id: 3,
            imageUrl: 'images/chatscreen/chatpeople/chatpeople-3.png',
            name: 'Robert Parker',
            message: 'ข้อความลูกค้า',
            sent_time: '11.28',
            unread_count: 12
        },
        {
            id: 4,
            imageUrl: 'images/chatscreen/chatpeople/chatpeople-4.png',
            name: 'Vivienne Westwood',
            message: 'ข้อความลูกค้า',
            sent_time: '11.28',
            unread_count: 12
        }
    ])

    const handleNav = (el: React.RefObject<HTMLLIElement>, token: string) => {
        const myList = document.querySelectorAll("li.nav-bar")
        Array.from(myList).forEach(function (sibling) {
            sibling.classList.remove("active");
            sibling.setAttribute("style", "border-color:unset")
        });
        if (token !== "linetoken") {
            el.current?.classList.add('active')
            el.current?.setAttribute("style", "border-color:#1877f2")
        } else {
            el.current?.classList.add('active')
            el.current?.setAttribute("style", "border-color:#06C755")
        }
    }

    const getBackground = (color: string) => {
        if (color === 'linetoken') {
            return '#06C755'
        } else {
            return '#1877f2'
        }
    }

    const clearSearchInput = () => {
        setSearchValue('')
    }

    const onChangeSearch = evt => {
        setSearchValue(evt.target.value)
    }

    return (
        <div data-testid="d-flex flex-column InboxFacebookChat">
            <div className='banner-zone'>
                <div className="search-box">
                    <img src="images/chatscreen/magnifierIcon.png" alt="magnifier" ></img>
                    <input onChange={onChangeSearch} id="searchinput" placeholder='Search' value={search_value} />
                    {
                        search_value !== undefined && search_value?.length > 0 ?
                            <img onClick={() => clearSearchInput()} className='clear-inbox' src="images/chatscreen/clear_inbox_search.png" alt="clear_inbox" width="15px" height="15px" />
                            :
                            <></>
                    }

                </div>
                <div className={`select-navigation ${platformtoken}`}>
                    <ul className={`d-flex`}>
                        <li
                            data-testid="testclicknav"
                            ref={liRef}
                            className="nav-bar active"
                            onClick={() => handleNav(liRef, platformtoken)}
                            style={{ borderColor: getBackground(platformtoken) }}
                        >
                            <span className=" select-navigation-text">เพื่อน</span>
                            <span data-testid="MyNoti" style={{ backgroundColor: getBackground(platformtoken) }} className="count-noti">1</span>
                        </li>
                        <li
                            ref={liRef2}
                            className="nav-bar"
                            onClick={() => handleNav(liRef2, platformtoken)}
                            style={{ borderColor: getBackground(platformtoken) }}
                        >
                            <span className=" select-navigation-text">ข้อความ</span>
                            <span style={{ backgroundColor: getBackground(platformtoken) }} className="count-noti">1</span>
                        </li>
                    </ul>
                </div>
            </div>

            <div className={`chat-list-zone ${platformtoken}`}>
                <ul>
                    {
                        inboxMessageList.map((inboxMessageLists, index) => {
                            return (
                                <li key={index}>
                                    <div onClick={props.onClickChatMessagePage} className={`d-flex chatlistzone-groupchat ${index === 0 ? 'active' : ''}`}>
                                        <div className='image-zone'>
                                            <img alt='chatpeople_1' src={inboxMessageLists.imageUrl} width="58px" />
                                        </div>
                                        <div className="group-text-chatlist">
                                            <div className="chat-list-zone-friendname">{inboxMessageLists.name}</div><div className="chat-list-zone-friendmesaage">{inboxMessageLists.message}</div>
                                        </div>

                                        <div className="chat-list-timeandnoi-group justify-content-center">
                                            <div className="chat-list-delivery-time">{inboxMessageLists.sent_time}</div>
                                            <span className="count-noti">{inboxMessageLists.unread_count}</span>
                                        </div>
                                    </div>
                                </li>
                            )
                        })}
                </ul>
            </div>
        </div>

    )

}

export default InboxFacebookChat;