import { useState } from 'react'
import './SideSharePane.scss'

interface SideSharePaneProps {
  onClickCloseSideBarShare: () => void
  isShowSideBarShare: boolean
  onClickShareSuccessTrigger: () => void
  onClickCloseSuccessTrigger: () => void
}

function SideSharePane(props: SideSharePaneProps) {

  let { onClickCloseSideBarShare, isShowSideBarShare, onClickShareSuccessTrigger, onClickCloseSuccessTrigger: onClickCloseSuccessTrigger } = props;

  const [showMessageBox, setShowMessageBox] = useState<boolean>(false)

  const [friendList, setfriendList] = useState([
    {
      id: '1',
      name: 'Smith Mathew',
      imageUrl: 'images/share_profile_pic/profile-pic1.png',
      isTick: false
    },
    {
      id: 'Monica Randawa',
      name: 'Smith Mathew',
      imageUrl: 'images/share_profile_pic/profile-pic2.png',
      isTick: false
    },
    {
      id: '3',
      name: 'John Walton',
      imageUrl: 'images/share_profile_pic/profile-pic3.png',
      isTick: false
    },
  ])

  const handleTick = (index) => {
    let myfriendList = friendList.slice()
    myfriendList[index].isTick = !myfriendList[index].isTick;
    setfriendList(myfriendList)

    if (findTick()) {
      setShowMessageBox(true)
    } else {
      setShowMessageBox(false)
    }
  }

  const findTick = () => {

    let isFound = false;

    for (let i = 0; i < friendList.length; i++) {
      if (friendList[i].isTick) {
        isFound = true;
      }
    }

    if (isFound) {
      return true
    } else {
      return false
    }

  }

  const handleShareButton = () => {
    const el = document.getElementById('sharedzone');
    el?.classList.remove('d-none');
    el?.focus();
  }

  const handleSuccessShare = (func) => {
    func();
  }

  const handleCloseSuccessShare = (func) => {
    const el = document.getElementById('sharedzone');
    el?.classList.add('d-none')
    func();
  }

  return (
    <>
      <div className={`d-flex flex-column side-sharepane-zone ${isShowSideBarShare ? 'show' : ''}`}>
        <div className="d-flex side-sharepane-header justify-content-space-between">
          <div className="back-step cursor-pointer" onClick={onClickCloseSideBarShare}>
            <img src="s/chatscreen/backstep_button.png" alt="backstep" width='23px' />
          </div>
          <div className="sidebar-headername">แชร์กับ(1)</div>
          <div onClick={handleShareButton} className="sidebar-sharelinkbutton cursor-pointer">แชร์</div>
        </div>
        <div className="side-sharepane-content">
          <div className={`mytextarea ${showMessageBox ? '' : 'd-none'}`}>
            <textarea placeholder='พิมพ์ข้อความ' name="message" id="message"></textarea>
          </div>
          <div className="search-box">
            <img src="images/chatscreen/magnifierIcon.png" alt="magnifier" ></img>
            <input placeholder='Search' />
          </div>
          <div className="friend-list-zone">
            <ul>
              {
                friendList.map((friendLists, index) => {
                  return (
                    <li key={index} className="d-flex">
                      <div className="friendlist-image">
                        <img src={friendLists.imageUrl} alt="friend_image" width='38px' />
                      </div>
                      <div className="friend-name">
                        {friendLists.name}
                      </div>
                      <div className="friend-tickzone cursor-pointer">
                        {friendLists.isTick ?
                          <img width="26px" height="26px" alt='tickzone_tick' src="images/icons/tick_contactlist.png" className="tickzone mr-2" onClick={() => handleTick(index)}></img>
                          :
                          <img width="26px" height="26px" alt='tickzone_untick' src="images/icons/untick_contactlist.png" className="tickzone mr-2" onClick={() => handleTick(index)}></img>
                        }
                      </div>
                    </li>
                  )
                })
              }
            </ul>
          </div>
        </div>
      </div>
      <div id='sharedzone' onBlur={() => handleCloseSuccessShare(onClickCloseSuccessTrigger)} onFocus={() => handleSuccessShare(onClickShareSuccessTrigger)} tabIndex={0} className="d-flex flex-column success-share-alert d-none">
        <div className='imagesucess-popup'>
          <img src="images/icons/tick_successpopup.png" alt="ticksucess_popup" width="187px" />
        </div>
        <div>
          <div className='success-popup-title'>
            แชร์สำเร็จ
          </div>
          <div className='success-popup-description'>
            ระบบได้บันทึกข้อมูลเรียบร้อยแล้ว
          </div>
        </div>
      </div>
    </>
  )
}

export default SideSharePane